<template>
  <BaseLayout title="物流详情">

    <van-cell v-if="typeof state.order.consignee == 'object'" class="consignee">
      <template #title>{{state.order.consignee.name}}（{{state.order.consignee.mobile}}）</template>
      <template #label>
        {{state.order.consignee.province}} {{state.order.consignee.city}}  {{state.order.consignee.county}} {{state.order.consignee.address}}
      </template>
    </van-cell>

    <van-tabs v-model:active="active" type="card" :color="Object.keys(state.list).length <= 1 ? '#F9F9F9' : ''" @click="onTab">
      <van-tab v-for="(v,k,index) in state.order.logistics" :key="k" :title="Object.keys(state.list).length > 1 ? '包裹 ' + (index + 1) : ''" :name="k"></van-tab>
    </van-tabs>
    
    <van-cell v-show="state.logistics.carrier && state.logistics.carrier.length >= 2" :title="state.logistics.carrier" :value="state.logistics.waybill">
      <template #right-icon><van-icon name="edit" size="18" @click="$copy(state.logistics.waybill)" /></template>
    </van-cell>
    <van-steps direction="vertical" :active="0">
      <van-step v-for="(item,index) in state.logistics.track" :key="index">
        <van-cell :title="item.msg" :label="item.dt"></van-cell>
      </van-step>
    </van-steps>

  </BaseLayout>
</template>

<style scoped>
.consignee {
  margin-bottom: 10px;
}
</style>

<script>
import {reactive,ref} from 'vue'
import BaseLayout from '@/components/BaseLayout.vue'

export default {
  components: {BaseLayout},

  props: {
    id: {
      type: [String, Number],
      default: 0,
    },
    lid: {
      type: [String, Number],
      default: 0,
    },
  },

  setup() {
    const state = reactive({
      order: {},
      list: [],
      logistics: [],
    });
    const active = ref(0);
    return {state};
  },

  created(){
    this.active = this.lid;
    this.onLoad();
  },

  methods: {
    onLoad() {
      this.$http.post("/mall/orderDetail?id=" + this.id).then((data) => {
        this.state.order = data;
        if (data.type == 1 && data.status > 0){
          this.$http.get("/jdmall/update?id=" + this.id);
        }
      });
      this.$http.post("/mall/orderTrack?oid=" + this.id).then((data) => {
        let arr = {};
        for (let i in data) {
          arr[data[i]["id"]] = data[i];
          arr[data[i]["id"]]["track"] = _.reverse(JSON.parse(data[i]["track"]));
        }
        this.state.list = arr;
        this.state.logistics = typeof arr[this.lid] != "undefined" ? arr[this.lid] : arr[_.keys(arr)[0]];
      });
    },

    onTab(index) {
      this.state.logistics = this.state.list[index];
    }
  }
}
</script>
